.tabs-container {
    width: 100%; 
    height: 50px;
    z-index: 99;
    background-color: #111922;
    position: absolute;
    left: 0;
    display: flex;
    justify-content: center;
    border-bottom: 3px solid #FF8D00;
}

.tabs-container.fixed {
    position: fixed;
    left: 0;
    top: 0;
    bottom: inherit;
}

.tabs-sub-container {
    max-width: 1200px;
    width: 100%;
    transition: height 0.3s ease-out
}

.tabs-menu-item {
    width: 80px;
    cursor: pointer;
    text-align: center;
    display: inline;
    color: white;
    line-height: 50px;
    margin: 0 20px;
    transition: color 0.5s;
}

.tabs-menu-item.active {
    color: #FF8D00;
}


@keyframes tabsDown {
    from {
        height: 0;
    } to {
        height: 50px;
    }
}