.works-tabs-item-container {
    flex-direction: row;
    position: relative;
    white-space: nowrap;
}

.works-tab-item {
    font-size: 13pt;
    margin: 0 25px;
    padding: 0 40px;
    cursor: pointer;
    font-weight: 600;
    transition: background-color 0.5s;
}

.works-tab-item.active {
    background-color: #C26B00;
    color: white;
}

.works-items-row-container {
    width: 100%;
    height: 300px;
}

.works-items-card-container {
    width: 100%;
    height: 100%;
    transition: all 600ms ease 0ms, opacity 600ms linear;
    display: inline-block;
    transform: translate(0px, 0px);
}

.works-items-card-img {
    width: 100%;
    height: 100%;
    transition: opacity 0.5s;
    background: url("http://findmatthew.com/style/../img/thumb-3.jpg") center center/cover;
}

.works-items-card-text {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 2;
    transition: all 0.3s ease-in-out;
    text-align: center;
    opacity: 0;
}

.works-items-card-btn {
    position: absolute;
    left: 0;
    right: 0;
    font-size: 13pt;
    padding: 7px 0;
    bottom: 0;
    width: 170px;
    z-index: 2;
    margin: 0 auto;
    color: #1b242f;
    transition: all 0.3s ease-in-out;
    border: 2px solid #FF8D00;
    opacity: 0;
}

.works-items-card-btn:hover {
    background: #FF8D00;
    cursor: pointer;
    color: white;
}